:root {
    --ff-header: 'Montserrat', sans-serif;
    --ff-paragraph:  'Roboto', sans-serif;

    /* --ff-startUP:;
    --ff-bank :helvetica; */
    --fw-reg: 400;
    --fw-med: 700;
    --fw-bold: 900;

    --clr-white:hsla(0, 0%, 100%, 0.8);
    --clr-black:hsla(0, 0%, 0%, 0.8);
    --clr-light: hsl(0,0%,100%);
    --clr-dark: hsl(0,0%,19%);
    --clr-accent: hsl(170,82%,48%);
    
    --fs-h1: 3rem;
    --fs-h2: 2.25rem;
    --fs-h3: 1.25rem;
    --fs-body: .9rem;

    
    --bs: 0.25em 0.25em 0.75em hsla(0, 0%, 0%, 0.25),
          0.125em 0.125em 0.25em hsla(0, 0%, 0%, 0.15);
}
html, body {
  height: 100%;
    scroll-behavior: smooth;
    margin: 0;

}
body {
    -webkit-text-size-adjust: 100%;
    min-height: 100vh;
    
    line-height: 1.6;
    -webkit-font-smoothing: antialiased;
    font-family: var(--ff-paragraph);
    font-size: var(--fs-body);
    background-color: var(--clr-light);
    display: flex;
    justify-content: center;
    align-items: center;
    
}
#root{
  width: 100%;
    
    display: flex;
    justify-content: center;
    align-items: center;
}
.App{
    width: 95%;
    min-height: 100vh;
    margin: 0 auto;
     max-width: 37.5em;    
    display: flex;
    flex-direction: column;
    place-content: center;
    place-items: center;
/* darkmode */
    /* background-color: var(--clr-dark); 
    color: var(--clr-white); */
/* lightmode */
    background-color: var(--clr-light); 
    color: var(--clr-black);
    padding:  1em;
    position: relative;
    overflow: hidden;
    border-bottom: 1px solid hsla(240, 100%, 90%,.5);
    border-left: 1px solid hsla(240, 100%, 90%,.5);
    border-top: 1px solid hsla(0, 100%, 90%,.5);
    border-right: 1px solid hsla(0, 100%, 90%,.5);
}

/* typography */
p{
    max-width: 700px;
    letter-spacing: 1px;
    padding: 1em;
    letter-spacing: 1.4;
}
h1 { font-size: var(--fs-h1); font-family: var(--ff-header);}
h2 { font-size: var(--fs-h2); font-family: var(--ff-header);}
h3 { font-size: var(--fs-h3); font-family: var(--ff-header);}
/*testing*/
h1,
h2,
h3 {
    line-height: 1.1;
    margin: 0;
    letter-spacing: -0.05em;
}
.Start{
  display: flex;
  flex-direction: column;
}
.Start-circle-1{
  position: absolute;
  background-color: red;
  width: 40vw;
  height: 40vh;
  top: 0%;
  right: 0%;
  border-radius: 59% 41% 30% 70% / 55% 31% 69% 45% ;
  transform: translate(45% ,-30%);
  animation: buble 2s infinite;
}
.Start-circle-2{
  position: absolute;
  background-color: blue;
  width: 40vw;
  height: 40vh;
  bottom: 0%;
  left: 0%;
  border-radius: 59% 41% 30% 70% / 55% 31% 69% 45% ;
  transform: translate(-40% ,50%);
  animation: buble 2s infinite  cubic-bezier(0.74, 0.2, 0.54, 0.87);
}
@keyframes buble {
  0%{
    border-radius: 59% 41% 30% 70% / 55% 31% 69% 45% ;  }
  10%{
    border-radius: 57% 43% 34% 66% / 55% 33% 67% 45% ;
  }
  20%{
    border-radius: 54% 46% 38% 62% / 55% 35% 65% 45% ;
  }
  30%{
    border-radius: 51% 49% 42% 58% / 55% 37% 63% 45% ;
  }
  40%{
    border-radius: 48% 52% 46% 54% / 55% 38% 62% 45% ;
  }
  50%{
    border-radius: 51% 49% 50% 50% / 55% 40% 60% 45% ;
  }
  60%{
    border-radius: 54% 46% 46% 54% / 55% 38% 62% 45% ;
  }
  70%{
    border-radius: 57% 43% 42% 58% / 55% 35% 65% 45% ;
  }
  80%{
    border-radius: 59% 41% 38% 62% / 55% 33% 67% 45% ;
  }
  90%{
    border-radius: 59% 41% 34% 66% / 55% 31% 69% 45% ;
  }
  100%{
    border-radius: 59% 41% 30% 70% / 55% 31% 69% 45% ;
  }

  
}

.Quiz{
  font-size: 0.7rem;
  z-index: 1;
  width: 100%;
}

.Quiz-answers{
  display: flex;
  border-bottom: 1px solid ;
  justify-content: space-between;
  z-index: 1;
  width: 100%;
}
.Quiz-answers >p {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
  background: hsla(0, 0%, 100%,.8);
  cursor: pointer;
  width: 25%;
}
.Quiz-answers >p:focus,
.Quiz-answers >p:hover{
  /* background-color: green; */

}
.Quiz-p{
  background: hsla(0, 0%, 100%,.8);
}
.answer-p{
  font-size: .8rem;
  line-height: 1.1;
  border-radius: 5px;
  margin: .5em;
}
.false{
  background-color: hsla(0, 70%, 70%,.5);
}
.true{
  background-color: hsla(120, 70%, 70%,.5);
}










































































